@font-face {
    src: url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
    font-family: 'Source Sans Pro', sans-serif;
}

body {
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 14px;
    font-weight: 400;
    margin: 0;
    padding: 0;
}
.conteiner {
    max-width: 1085px;
    margin: 0 auto;
    background: rgba(138, 138, 138, 0.5);
}
.nav {
    background: rgb(117, 117, 117);
    padding: 0 5rem;
}
.menu {
    list-style: none;
    margin: 0;
    padding: 0;
}

.menu-1 {
    display: inline-block;
    position: relative;
}

.menu-link {
    text-decoration: none;
    padding: 10px 65px;
    background-color: #555555;
    color: #ffffff;
}

.menu-link:hover {
    background-color: #15576f;
    cursor: pointer;
}

.submenu {
    display: none;
    position: absolute;
    left: 0;
    top: 85%;
    background-color: #555555;
    border: 1px solid #000000;
    padding: 10px;
    width: 86%;
    z-index: 1;
}

.submenu-item {
    display: block;
}

.submenu-link {
    padding: 10px 0px;
    display: block;
    color: #ffffff;
    text-decoration: none;
}

.submenu-link:hover {
    text-decoration: underline;
}

.menu>.menu-1:hover>.submenu {
    display: block;
}

.main {
    padding: 0 5rem 3rem;
    &__post {
        margin: 3rem 0 0 0;
        &__titel {
            background-color: #555555;
            color: #ffffff;
            padding: 5px 10px;
        }
        &__time {
            opacity: 0.5;
        }
        &__button {
            color: #ffffff;
            background-color: rgba(85, 85, 85, 0.6);
            border: 1px solid #000000;
            padding: 5px;
        }
        &__category {
            display: flex;
            align-items: center;
            &__titel {
                margin-right: 5px;
            }
        }
        &__author {
            display: flex;
            align-items: center;
            &__titel {
                margin-right: 5px;
            }
        }
        &__teg {
            display: flex;
            align-items: center;
            &__titel {
                margin-right: 5px;
            }
        }
    }
}